.san-pham-ct
    .swiper-navigation
        display: none
    .product-thumb-wrapper
        padding: 30px 0
        .swiper-container
            .image, img
                height: 100%
                width: 100%
                object-fit: cover
        .swiper-slide
            transition: .4s ease-in-out all
            opacity: .3
            position: relative
            &:after
                content: ''
                width: 100%
                height: 100%
                box-shadow: 0 0 0 0 inset #ac1f23
                position: absolute
                top: 0
                transition: .4s ease-in-out all
                left: 0
            &.swiper-slide-thumb-active
                opacity: 1
                &:after
                    box-shadow: 0 0 0 2px inset #ac1f23
    .product-active-wrapper
        .swiper-container
            height: 100%
            *
                height: 100%
            img
                width: 100%
                object-fit: cover
    .information-wrapper
        .name
            margin-bottom: 10px
        .desc
            font-weight: 500
            color: #666666
        .price
            margin-top: 20px
            font-size: 30px
            font-weight: 700
            color: #ac1f23
        .mg-button
            margin-top: 20px
            display: inline-block
            padding: 10px 60px
            border-radius: 50px
    .social-share
        display: flex
        align-items: center
        .zalo-share
            margin-left: 10px
    .detail-wrapper
        margin-top: 30px
        .detail-tab
            border: 1px solid #d7d7d7
            border-bottom: 0
            display: flex
            .item
                user-select: none
                padding: 8px 20px
                cursor: pointer
                flex-grow: 1
                text-transform: uppercase
                font-weight: 600
                text-align: center
                transition: .4s ease-in-out all
                color: #ac1f23
                &.active
                    background: #ac1f23
                    color: #fff
                + .item
                    border-left: 1px solid #d7d7d7
        .detail-content
            padding: 30px
            border: 1px solid #d7d7d7
            .item
                display: none
                margin: 0 -10px
                p
                    flex: 0 0 50%
                    margin-bottom: 10px
                    // padding: 0 10px
                &.active
                    display: block
                    @media(min-width: 768px)
                        // display: flex
                        // p
                        //     margin-bottom: 0
            // .text-wrapper
            //     font-size: 14px
            //     font-weight: 500
            //     p
            //         + p
            //             margin-top: 10px
            // .image-wrapper
            //     margin-top: 20px
            //     img
            //         width: 100%
    .related-wrapper
        margin-top: 30px
        .mg-title
            text-align: left
        .swiper-container
            padding: 10px
            margin: -10px
    @media(min-width: 768px)
        .detail-wrapper
            .detail-tab
                display: inline-flex
                .item
                    padding: 10px 30px
            .detail-content
                padding: 50px
    @media(min-width: 1025px)
        .detail-wrapper
            .detail-content
                .image-wrapper
                    height: 100%
                    margin-top: 0
                    img
                        height: 100%
        .product-image-wrapper
            display: flex
        .product-thumb-wrapper
            position: relative
            order: 1
            .swiper-container
                height: 400px
            flex: 0 0 15%
        .product-active-wrapper
            order: 2
            padding-left: 25px
            flex: 0 0 85%
            max-width: 85%
        .swiper-navigation
            display: block
            .swiper-btn
                position: absolute
                height: 30px
                font-size: 24px
                left: 0
                width: 100%
                display: flex
                justify-content: center
                cursor: pointer
                align-items: center
                &:focus
                    outline: none
                &:hover
                    background: rgba(#000, .1)
                &.swiper-next
                    bottom: 0
                &.swiper-prev
                    top: 0