.home-tintuc
	padding: 60px 0
	@media (max-width: 786px)
		padding: 40px 0
	.tintuc-title
		margin-bottom: 40px
		h2 
			font-size: 30px 
			text-transform: uppercase
			color: #333333
	.slide
		border-radius: 10px
		position: relative
		padding: 50px
		background: #ffffff
		.swiper-wrapper
			padding-bottom: 30px
			.swiper-slide
				figure 
					width: 100%
					.img 
						padding-bottom: 20px
						img 
							object-fit: cover
							width: 100%
					figcaption
						width: 100%
						time
							padding-bottom: 20px
						h3 
							font-size: 18px
							color: #333333
							font-weight: 600
							margin: 20px 0 50px 
							transition: .4s ease-in-out all
							overflow: hidden
							display: -webkit-box
							text-overflow: ellipsis
							-webkit-line-clamp: 2
							-webkit-box-orient: vertical
						time
							font-size: 12px
							color: rgb(153, 153, 153)
						a
							color: inherit
						.button-more
							padding: 15px 50px
							background: #ac1f23
							border-radius: 25px 
							font-size: 14px
							color: #ffffff
							transition: .4s ease-in-out all
							font-weight: 600
							text-transform: uppercase
							em 
								margin-left: 10px
							&:hover
								box-shadow: 0 0 0 2px inset #ac1f23
								background: none
								color: #ac1f23
						@media (max-width: 480px)
							.button-more
								padding: 10px 20px
					&:hover
						figcaption
							h3
								color: #ac1f23
	.swiper-pagination
		display: flex
		width: calc(100% - 100px)
		justify-content: center
		align-items: center
		.swiper-pagination-bullet
			width: 0
			transition: .4s ease-in-out all
			height: 0
			margin: 0 5px
			border: 3px solid #666
			opacity: 1
			&.swiper-pagination-bullet-active
				width: 10px
				height: 10px
				border: 2px solid #666
				background: none