.thu-vien
    .list-item
        margin-top: 30px
        .image
            margin-bottom: 30px
            position: relative
            .active
                background: #000
                height: 100%
                width: 100%
                img
                    opacity: .5
                    height: 100%
                    transition: .4s ease-in-out all
                    width: 100%
                    object-fit: cover
            .play
                position: absolute
                top: 50%
                left: 50%
                pointer-events: none
                transition: .4s ease-in-out all
                transform: translate(-50%, -50%)
            &:hover
                .play
                    opacity: 0
                .active
                    img
                        opacity: 1