.main-breadcrumb
    // background: #f5f5f5
    position: relative
    .breadcrumb-absolute
        position: absolute
        bottom: 0
        padding: 10px 0
        left: 0
        width: 100%
        background: rgba(0,0,0,.3)
    .image
        img
            width: 100%
    ol.breadcrumb
        display: flex
        align-items: center
        list-style: none
        padding-left: 0
        li
            display: flex
            align-items: center
            a
                position: relative
                padding: 0 10px
                color: #fff
                font-size: 14px
                &.active
                    color: #fff
            + li
                a
                    &:before
                        content: ''
                        height: 12px
                        position: absolute
                        width: 1px
                        background: #ffffff
                        top: 50%
                        left: 0
                        transform: translateY(-50%)