.home-search
    padding: 40px 0 
    background: #ac1f23
    .title
        text-align: center
        padding-bottom: 20px
        h2 
            font-weight: 600
            font-size: 18px
            color: #ffffff
            text-transform: uppercase
    .search-wrap 
        display: flex
        justify-content: space-around
        flex-wrap: wrap
        .select
            overflow: hidden
            width: 330px
            height: 50px
            border-radius: 5px
            position: relative
            &:after
                content: '\f140'
                font-size: 18px
                color: #666666
                right: 30px
                position: absolute
                top: 50%
                pointer-events: none
                transform: translateY(-50%)
                font: normal normal normal 24px/1 Material Design Icons
            &:first-child
                border-top-left-radius: 25px
                border-bottom-left-radius: 25px
        select
            width: 100%
            height: 100%
            background: #ffffff
            border: none
            color: #666666
            color: rgba(#333333, .5)
            font-size: 14px
            font-weight: 600
            outline: none
            padding: 0 20px
            -webkit-appearance: none
            -moz-appearance: none
        .btn-search 
            
            border-top-right-radius: 25px
            border-bottom-right-radius: 25px
            width: 100px
            height: 50px
            background: #cf2435
            display: flex
            align-items: center
            justify-content: center
            em 
                color: #ffffff
    @media (max-width: 1200px)
        .search-wrap
            flex-direction: column
            align-items: center
            .select,.btn-search
                margin: 10px 0
                border-radius: 0!important
            .btn-search
                width: 330px


