.gioi-thieu-1
	padding-bottom: 140px
	.row
		&>div
			align-self: center
	.video
		position: relative
		.background
			img
				width: 100%
		.play
			position: absolute
			border-radius: 50%
			top: 50%
			left: 50%
			transform: translate(-50%, -50%)
			&:after, &:before
				content: ''
				width: 100%
				height: 100%
				border-radius: 50%
				border: 1px solid #ac1f23
				position: absolute
				top: 50%
				left: 50%
				transform: translate(-50%, -50%)
				-webkit-animation: linear 2s infinite CircleAnimate
				animation: linear 2s infinite CircleAnimate
			&:after
				animation-delay: .5s
	.content
		margin-top: 20px
		text-align: left
		h3
			text-transform: uppercase
			color: #ac1f23
			font-size: 22px
			font-weight: 600
			margin-bottom: 10px
		p
			font-size: 15px
	@media(min-width: 1025px)
		.content
			margin-top: 0
			h3
				margin-bottom: 30px
		.video
			&:before
				content: ''
				width: 2000px
				right: -30px
				top: 60px
				background: #ebebeb
				position: absolute
				height: 100%
			img
				position: relative
.gioi-thieu-2
	color: #ffffff
	.item
		text-align: center
		border-left: 1px solid rgba(#fff,.3)
		padding: 20px 0
		h3
			font-size: 60px
			span
				font-size: 15px
			sup
				font-size: 8px
		p
			font-size: 15px
		&:last-child
			border-right: 1px solid rgba(#fff,.3)
		@media(max-width: 1025px)
			border: 0
			border-bottom: 1px solid rgba(#fff,.3)
			&:last-child
				border: 0
.gioi-thieu-3
	display: none
	a
		display: block
		position: relative
	.image
		img
			width: 100%
	.play
		position: absolute
		top: 50%
		left: 50%
		transform: translate(-50%, -50%)
.gioi-thieu-4
	.figure-wrapper
		padding-bottom: 50px
		@media screen and(max-width:1441px)
			padding-bottom: 25px
		@media screen and(max-width:1025px)
			padding-bottom: 20px

	figure
		height: 100%
		box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.05)
		background: #ffffff
		.image
			flex: 0 0 100%
			display: flex
			justify-content: center
			align-items: center
			img
				max-width: 100%
				height: auto
				// height: 100%
				// object-fit: cover
				// width: 100%
		.content
			display: flex
			justify-content: center
			// align-items: center
			flex-direction: column
			flex: 0 0 100%
			padding: 0 30px 50px 30px
			background: #ffffff
			text-align: left
			h5
				min-height: 54px
				margin-bottom: 10px
				font-size: 18px
				color: #ac1f23
				text-transform: uppercase
			p
				font-size: 15px
	@media screen and(max-width:1200px)
		figure
			.content
				padding: 0 20px 40px 20px
				h5
					font-size: 16px
	@media screen and(max-width:1025px)
		.col-lg-3
			margin-bottom: 30px
		figure
			.content
				padding: 0 15px 30px 15px
				h5
					min-height: 100%
