.link-to-section
	padding: 50px 0  0 0
	background: #ffffff
	@media screen and(max-width:1025px)
		padding: 50px 0 0 0
	.col-lg-4
		&:nth-child(2)
			.cards
				.card-image
					position: relative
					&:before
						pointer-events: none
						content: ''
						position: absolute
						display: inline-block
						background-image: url('gioi-thieu/play.png')
						background-size: contain
						background-repeat: no-repeat
						width: 60px
						height: 60px
						left: 50%
						top: 50%
						transform: translate(-50%,-50%)
	.cards
		overflow: hidden
		position: relative

		
		.card-image
			a
				display: flex
				flex: auto
				width: 350px
				img
					width: 100%
					height: auto
		.card-caption
			pointer-events: none
			background: rgba(#000000,.3)
			width: 100%
			padding: 10px 0
			position: absolute
			bottom: 0
			left: 50%
			transform: translateX(-50%)
			p
				display: flex
				justify-content: center
				align-items: center
				font-weight: 600
				color: #ffffff
				font-size: 18px
				text-transform: uppercase
	@media screen and(max-width:1025px)
		.col-lg-4
			margin-bottom: 30px
	@media screen and(max-width:576px)
		.cards
			.card-image
				a
					width: 100%
					img
						max-width: 100%
						height: auto
