.tin-tuc
    .news-list
        margin-top: 30px
.news-item
    margin-bottom: 50px
    figure
        .image
            img
                width: 100%
                height: 100%
                object-fit: cover
        figcaption
            padding-top: 10px
        time
            font-size: 12px
            margin-bottom: 5px
            display: block
            color: #989898
            font-weight: 500
        h4
            margin-bottom: 20px
            transition: .4s ease-in-out all
            font-weight: 600
            font-size: 18px
            display: -webkit-box
            -webkit-box-orient: vertical
            -webkit-line-clamp: 3
            overflow: hidden
    &:hover
        figure
            h4
                color: #ac1f23