header
    background: #f5f5f5
    width: 100%
    position: fixed
    z-index: 999
    &.fixed
        box-shadow: 0 0 8px rgba(0,0,0,.15)
    .menu-wrapper
        margin: 0 -50px
        display: flex
        align-items: center
        padding: 10px 0
        position: relative
        .minhgiang-logo
        .main-menu 
            margin-left: auto
            ul 
                list-style: none 
                li 
                    display: inline-block
                    border-right: 1px solid #e2e2e2
                    >a 
                        font-size: 12px
                        text-transform: uppercase
                        color: #333333
                        font-weight: bold
                        padding: 36px 10px
                        position: relative
                        &:after
                            content: ''
                            display: block
                            position: absolute
                            width: 100%
                            bottom: 0
                            height: 2px
                            background: #ac1f23
                            opacity: 0
                        &:before
                            content: ''
                            display: block
                            position: absolute
                            width: 0
                            height: 0
                            bottom: 0
                            left: 50%
                            transform: translateX(-50%)
                            border-left: 5px solid transparent
                            border-right: 5px solid transparent
                            border-bottom: 6px solid #ac1f23
                            opacity: 0
                    &:hover, &.active
                        a 
                            color: #ac1f23
                            &:before,&:after
                                opacity: 1
        .formsearch
            margin-left: 20px
            z-index: 99
            .iconsearch
                width: 30px
                height: 30px
                background: none
                border: 2px solid #999999 
                border-radius: 50%
                display: flex 
                align-items: center
                justify-content: center
                em 
                    font-size: 14px
                    color: #ac1f23
            .searchbox
                position: absolute
                bottom: -54px
                pointer-events: none
                right: 50%
                transform: translate(50%)
                display: flex
                align-items: center
                padding: 10px
                opacity: 0
                transition: all .3s ease-in-out
                margin-left: auto
                background: #ffff
                width: 100% 
                opacity: 0
                box-shadow: 0 4px 5px rgba(0,0,0,.1)
                &.active 
                    opacity: 1
                    pointer-events: initial
                // &:before
                //     content: ""
                //     position: absolute
                //     width: 0
                //     height: 0
                //     border-left: 10px solid transparent
                //     border-right: 10px solid transparent
                //     border-bottom: 10px solid #ffff
                //     top: -9px
                //     right: 5px
                input[type="text"]
                    width: 100%
                    padding: 5px 10px
                    background: none
                    border: none
                    border-bottom: 1px solid #ac1f23
                    outline: none
                .button-search
                    position: absolute
                    right: 20px
                    em 
                        color: #ac1f23

        .language 
            margin-left: 10px
            position: relative
            .lang-active
                width: 30px
                height: 30px
                border-radius: 50px
                border: 2px solid #999999
                justify-content: center
                align-items: center
                display: flex
                color: #999999
                font-weight: 500
                font-size: 12px
            .lang-list
                position: absolute
                list-style: none
                top: 60px
                left: -20px
                width: 80px
                text-align: center
                background: #ffff
                box-shadow: 0 4px 5px rgba(0,0,0,.1)
                display: none
                z-index: 99
                li 
                    a 
                        font-size: 12px
                        color: #ac1f23
            
        .call
            margin-left: 10px
            height: 35px
            width: 180px
            background: #1b4981
            border-radius: 50px
            display: flex
            font-weight: 700
            justify-content: center
            align-items: center
            @media(max-width: 1200px)
                margin-left: 0!important
            span,em
                color: #ffff
                font-size: 15px
            em 
                margin-right: 10px
        .sidebar-menu
            height: calc(100vh-88px)
            width: 300px
            right: -300px
            background: #ac1f23
            z-index: 98
            top: 88px
            position: fixed
            transition: .25s ease-in-out
            &.active
                right: 0
                .call
                    display: block
                .main-menu
                    display: block
        .toggle-button
            display: none
            .hamburger--boring
                outline: none
                padding: 30px
                .hamburger-box
                    .hamburger-inner
                        width: 30px
                        &:after
                            width: 30px
                        &:before
                            width: 30px
    .has-mega
        em
            display: none
        &:hover
            .mega-wrapper
                opacity: 1
                top: 100%
                pointer-events: initial
    .mega-back
        display: none
    .mega-wrapper
        opacity: 0
        pointer-events: none
        position: absolute
        transition: .4s ease-in-out all
        top: 150%
        width: 1110px
        display: flex
        flex-wrap: wrap
        padding: 30px
        padding-bottom: 20px
        left: 50%
        transform: translateX(-50%)
        // margin: 0 -5px
        background: #fff
        box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.2)
        .mega-item
            margin-bottom: 10px
            flex: 0 0 33.3333%
            padding: 0 5px
            figure
                position: relative
                .image
                    background: #ac1f23
                    width: 100%
                    img
                        width: 100%
                        transition: .4s ease-in-out all
                figcaption
                    position: absolute
                    bottom: 0
                    left: 0
                    height: 50px
                    padding: 10px
                    color: #fff
                    width: 100%
                    transition: .4s ease-in-out all
                    display: flex
                    justify-content: center
                    align-items: center
                    h4
                        font-size: 18px
                        text-transform: uppercase
                        font-weight: 600
            &:hover
                figure
                    figcaption
                        height: 100%
                    .image
                        img
                            opacity: .2
@media (max-width: 1200px)
    header
        .has-mega
            display: flex!important
            align-items: center
            justify-content: space-between
            .mega-show
                order: 2
                color: #ffffff
                width: 30px
                display: inline-block
                font-size: 16px
                text-align: center
        .mega-back
            display: flex
            margin-bottom: 20px
            align-items: center
            text-transform: uppercase
            em
                font-size: 16px
                width: 30px
                text-align: center
                display: inline-block

        .mega-wrapper
            overflow-y: auto
            position: fixed
            top: 88px!important
            opacity: 1
            pointer-events: initial
            height: calc(100vh - 88px)
            width: 300px
            right: -300px
            left: initial
            transform: initial
            z-index: 333
            transition: .4s ease-in-out all
            &.active
                right: 0
            .mega-item
                flex: 0 0 100%
        .menu-wrapper
            margin: 0
            .call
                display: none
            .main-menu
                display: none
            .sidebar-menu
                &.active
                    .main-menu
                        ul 
                            padding: 0
                            li 
                                display: block 
                                padding: 15px 30px
                                position: relative
                                border-bottom: 1px solid #b33333
                                a 
                                    color: #ffff
                                    padding: 0
                                    &:after,&:before
                                        display: none
                                // &:hover
                                //     border-right: 0
                                //     border-left: 4px solid #ac1f23
                                //     background: #ffff
                                //     transition: all .5s ease-in-out
                                //     &:before
                                //         content: ''
                                //         position: absolute
                                //         display: block
                                //         left: -1px
                                //         top: 50%
                                //         transform: translateY(-50%)
                                //         width: 0
                                //         height: 0
                                //         border-top: 5px solid transparent
                                //         border-left: 5px solid #ac1f23
                                //         border-bottom: 5px solid transparent
                                //     a 
                                //         color: #ac1f23
                    .call
                        bottom: 0
                        position: absolute
                        border-radius: 0
                        align-items: center
                        display: flex
                        width: 100%
            .formsearch
                margin-left: auto
                margin-right: 10px
            .language
                margin-right: 20px
            .toggle-button
                order: 1
                width: 2rem
                height: 2rem
                display: flex
                margin-left: 10px
                justify-content: center
                align-items: center
                .button
                    height: 2px
                    width: 30px
                    background: #333
                    position: relative
                    transition: all 0ms 300ms
                    &:after
                        content: ""
                        position: absolute
                        left: 0
                        width: 100%
                        height: 100%
                        bottom: 8px
                        background: #333
                        transition: bottom 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1)
                    &:before
                        content: ""
                        position: absolute
                        left: 0
                        width: 100%
                        height: 100%
                        top: 8px
                        background: #333
                        transition: bottom 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1)
                &.active
                    .button
                        background: transparent
                        &:before
                            top: 0
                            transform: rotate(45deg)
                            transition: top 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1)
                        &:after
                            bottom: 0
                            transform: rotate(-45deg)
                            transition: bottom 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1)