
.modulepager
    .pagination
        display: flex
        list-style-type: none
        justify-content: center
        li
            margin: 0 5px
            background: #f5f5f5
            color: #333333
            border-radius: 50%
            overflow: hidden
            &:last-child
                margin-right: 0
            a, span
                font-size: 14px
                width: 38px
                color: #333
                height: 38px
                display: flex
                justify-content: center
                align-items: center
            .LastPage, .NextPage, .BackPage, .FirstPage
                display: none
            &.active, &:hover
                background: #ac1f23
                color: #fff
                a, span
                    color: #fff