.san-pham-ds
    .zone-wrapper
        padding-bottom: 30px
        .zone-group
            padding: 6px 0
            .zone-group-heading
                font-size: 16px
                font-weight: 600
                display: flex
                cursor: pointer
                align-items: center
                &:before
                    content: '\f13c'
                    font: normal normal normal 24px/1 Material Design Icons
                    display: inline-block
                    font-size: 16px
                    margin-right: 10px
                    transition: .4s ease-in-out all
                &.active
                    &:before
                        transform: rotate(180deg)
            .zone-group-list
                display: none
                padding-top: 10px
                .zone-group-item
                    padding-left: 26px
                    a
                        padding-bottom: 10px
                        line-height: 1
                        transition: .4s ease-in-out all
                        font-size: 15px
                        display: inline-block
                        color: #999999
                        position: relative
                        &:after
                            content: ''
                            width: 0
                            height: 1px
                            background: #ac1f23
                            position: absolute
                            bottom: 8px 
                            left: 0
                            transition: .4s ease-in-out all

                    &.active, &:hover
                        a
                            color: #ac1f23
                            &:after
                                width: 100%
    .filter-wrapper
        .filter-group
            padding: 30px 0
            border-top: 1px solid #e1e1e1
            .filter-heading
                font-size: 16px
                font-weight: 600
                color: #ac1f23
                margin-bottom: 10px
            .filter-select
                position: relative
                font-size: 14px
                color: #666666
                font-weight: 500
            .select-heading
                user-select: none
                cursor: pointer
                background: #fff
                position: relative
                padding: 10px 20px
                border: 1px solid #e1e1e1
                width: 100%
                &:after
                    content: '\f140'
                    font: normal normal normal 24px/1 Material Design Icons
                    position: absolute
                    top: 50%
                    font-size: 18px
                    color: #666666
                    right: 20px
                    transform: translateY(-50%)
            .select-list
                position: absolute
                top: 100%
                left: 0
                max-height: 200px
                overflow-y: auto
                opacity: 0
                pointer-events: none
                transition: .4s ease-in-out all
                background: #ffffff
                z-index: 22
                width: 100%
                box-shadow: 0 0 5px rgba(#000,.1)
                .select-item
                    display: block
                    border-bottom: 1px solid #f1f1f1
                    padding: 10px 20px
                &.active
                    pointer-events: initial
                    opacity: 1
                .select-item
                    transition: .4s ease-in-out all
                    &:hover, &.active
                        background: #ac1f23
                        color: #fff
    .product-wrapper
        .product-heading
            padding: 10px 0
            border-top: 1px solid #e1e1e1
            border-bottom: 1px solid #e1e1e1
            .product-order
                span
                    display: block
                    margin-bottom: 10px
                .order-select
                    margin-bottom: 10px
                    position: relative
                .select-heading
                    display: flex
                    align-items: center
                    user-select: none
                    cursor: pointer
                    background: #ac1f23
                    position: relative
                    border-radius: 5px
                    overflow: hidden
                    padding: 8px 10px
                    padding-right: 50px
                    color: #fff
                    height: 40px
                    font-size: 13px
                    font-weight: 500
                    // border: 1px solid #e1e1e1
                    width: 100%
                    &:after
                        content: '\f35d'
                        font: normal normal normal 24px/1 Material Design Icons
                        position: absolute
                        width: 40px
                        height: 40px
                        top: 50%
                        background: #710c0e
                        display: flex
                        justify-content: center
                        align-items: center
                        font-size: 24px
                        color: #fff
                        right: 0
                        transform: translateY(-50%)
                .select-list
                    opacity: 0
                    pointer-events: none
                    transition: .4s ease-in-out all
                    position: absolute
                    top: 100%
                    left: 0
                    max-height: 200px
                    overflow-y: auto
                    background: #ffffff
                    z-index: 22
                    width: 100%
                    box-shadow: 0 0 5px rgba(#000,.1)
                    .select-item
                        display: block
                        border-bottom: 1px solid #f1f1f1
                        padding: 10px 20px
                    &.active
                        pointer-events: initial
                        opacity: 1
                    .select-item
                        transition: .4s ease-in-out all
                        &:hover, &.active
                            background: #ac1f23
                            color: #fff
            .product-pagesize
                display: flex
                align-items: center
                span
                    font-size: 14px
                .pagesize-list
                    display: flex
                    a
                        padding: 0 10px
                        line-height: 1
                        &.active
                            font-weight: 600
                            color: #ac1f23
                        + a
                            border-left: 1px solid #333
        .product-list
            margin-top: 30px
            .row
                margin: 0 -5px
                &>div
                    padding: 0 5px
                    margin-bottom: 10px
        @media(min-width: 768px)
            .product-heading
                display: flex
                padding: 15px 0
                align-items: center
                h2
                    margin-bottom: 0
                .product-order
                    margin-left: auto
                    display: flex
                    align-items: center
                    .order-select
                        margin-bottom: 0
                        min-width: 170px
                        margin-left: 10px
                    span
                        margin-bottom: 0
                .product-pagesize
                    margin-left: 20px
.product-item
    figure
        transition: .4s ease-in-out all
        .image
            img
                width: 100%
                height: 100%
                object-fit: cover
        figcaption
            background: #f5f5f5
            padding: 10px
            @media(min-width: 1025px)
                padding: 20px
            .heading
                transition: .4s ease-in-out all
                padding-bottom: 5px
                border-bottom: 1px solid #fff
                h4 
                    font-size: 16px
                    font-weight: 500
                p 
                    font-size: 18px
                    font-weight: 600
                    color: #ac1f23
            .footer
                padding-top: 10px
                ul
                    list-style: none
                    padding-left: 0
                    li
                        color: #999999
                a
                    display: block
                p
                    text-transform: uppercase
                    font-weight: 600
                    text-decoration: underline
                    margin-top: 10px
        &:hover
            box-shadow: 0px 0px 7.52px 0.48px rgba(0, 0, 0, 0.36)
            figcaption
                .heading
                    h4
                        text-decoration: underline