.dai-ly
    .wrap-dealer
        // display: flex
        align-items: center
        span
            margin-right: 10px
        .dealer-button
            display: none
        .form-group.dealer-country, .form-group.dealer-province, .form-group.dealer-district
            position: relative
            height: 40px
            margin-bottom: 10px
            border-radius: 5px
            overflow: hidden
            width: 100%
            span
                display: none
            select
                padding: 0 20px
                background: #ac1f23
                border: 0
                height: 100%
                width: 100%
                color: #fff
                font-size: 15px
                -webkit-appearance: none
                -moz-appearance: none
            &:after
                content: '\f0dd'
                width: 40px
                pointer-events: none
                height: 40px
                position: absolute
                top: 0
                right: 0
                display: flex
                justify-content: center
                // align-items: center
                padding-top: 5px
                background: #710c0e
                color: #fff
                font-family: Font Awesome\ 5 Free
                font-weight: 900
    .list-item
        margin-top: 30px
        .item
            margin-bottom: 30px
        .dealer-item
            height: 100%
            display: block
            background: #f5f5f5
            color: #333333
            font-size: 14px
            transition: .4s ease-in-out all
            padding: 30px
            a
                color: #333
            h5
                text-transform: uppercase
                font-size: 15px
                font-weight: 500
                transition: .4s ease-in-out all
                color: #ac1f23
                margin-bottom: 10px
            &:hover
                background: #ac1f23
                color: #fff
                h5
                    color: #fff
    @media(min-width: 768px)
        .wrap-dealer
            justify-content: center
            display: flex
            align-items: center
            .form-group.dealer-country, .form-group.dealer-province, .form-group.dealer-district
                width: 260px
                margin: 0 10px