.home-thuonghieu
    padding: 60px 0 
    @media (max-width: 768px)
        padding: 40px 0
    .title
        padding-bottom: 40px
        h2 
            font-size: 30px
            text-transform: uppercase
            color: #333333
    .thuonghieu-wrap
        display: flex
        flex-wrap: wrap
        img
            max-width: 100%
        .item
            display: flex
            justify-content: center
            transition: .4s ease-in-out all
            align-items: center
            border: 2px solid transparent
            &:hover 
                background: #fff
                border: 2px solid rgb(229, 229, 229)
                box-shadow: 0px 5px 20px 0px rgba(0, 0, 0, 0.05)