.san-pham
    padding-bottom: 20px
    @media(min-width: 1025px)
        padding-bottom: 50px
.zone-item
    margin-bottom: 30px
    .image
        img
            width: 100%
    figure
        transition: .4s ease-in-out box-shadow
        box-shadow: 0px 5px 25px 0px rgba(56, 50, 44, 0.25) 
        &:hover
            .image
                &:after
                    box-shadow: 0 0 0 1px inset #ac1f23
            figcaption
                background: #ac1f23
                h5
                    color: #fff
    .image
        position: relative
        &:after
            content: ''
            width: 100%
            transition: .4s ease-in-out all
            height: 100%
            position: absolute
            top: 0
            left: 0
            pointer-events: none
    figcaption
        padding: 10px
        transition: .4s ease-in-out all
        // background: #ac1f23
        text-align: center
        h5
            text-transform: uppercase
            color: #ac1f23
            transition: .4s ease-in-out all
            font-size: 18px
            font-weight: 500