.commentpanel
    input, textarea
        padding: 10px 20px
        border: 1px solid #d7d7d7
    textarea
        width: 100%
        height: 100px
    ul
        list-style-type: none
    .comment-text
        padding: 20px
        border: 1px solid #f1f1f1
        margin: 15px 0
    .journalTools
        padding-bottom: 30px
        @media(min-width: 1025px)
            padding-bottom: 50px
    .journalfooter
        text-align: right
        font-size: 13px
        color: #999
        font-weight: 500
        >*
            margin-left: 10px
    .jcmt
        margin-top: 20px
    .journalInfo, .cmtinfo
        margin: 0 -5px
        display: flex
        flex-wrap: wrap
        >*
            margin: 0 5px
            flex-grow: 1
            flex-basis: 0
            @media(max-width: 768px)
                flex: 0 0 100%
                margin-bottom: 10px
                padding: 10px 20px
        a
            display: flex
            align-items: center
            justify-content: center
            text-align: center
            background: #ac1f23
            color: #fff
            text-transform: uppercase
    .journalPlaceholder, .editorPlaceholder
        font-size: 13px
        color: #666666
        margin-top: 10px
    .journalsummary
        strong
            color: #ac1f23
            text-transform: uppercase
            font-weight: 600
        b
            font-size: 12px
            font-weight: 500
            color: #999
    .cmreply
        display: none
    .journalrow
        margin-bottom: 20px
    .journalItems
        max-height: 500px
        overflow-y: auto
    .jsummary
        padding-left: 50px
        strong
            color: #ac1f23
            text-transform: uppercase
            font-weight: 600
        b
            font-size: 12px
            font-weight: 500
            color: #999