.home-dai-ly
    .daily-bg
        height: 710px
        display: flex
        align-items: center
        position: relative
        img 
            width: 100%
        @media(max-width: 768px)
            background-position: left center!important
        .box-text
            h2 
                font-size: 30px
                font-weight: 600
                text-transform: uppercase
                color: #ffffff
                padding-bottom: 30px
            p 
                max-width: 500px
                font-size: 18px
                color: #ffffff
                padding-bottom: 50px
        
            .btn
                padding-bottom: 20px
                .button-1
                    a
                        background: #ac1f23
                        padding: 10px 30px
                        border-radius: 25px
                        color: #ffffff
                        font-weight: bold
                        font-size: 14px
                        text-transform: uppercase
                        margin-bottom: 20px
                        display: inline-flex
                        align-items: center
                        transition: .4s ease-in-out all
                        em 
                            font-size: 20px
                            margin-right: 10px
                        &:hover
                            background: #6c1013
                .button-2
                    a
                        background: #1b4981
                        padding: 10px 30px
                        border-radius: 25px
                        color: #ffffff
                        font-weight: bold
                        font-size: 14px
                        text-transform: uppercase
                        margin-bottom: 20px
                        display: inline-flex
                        align-items: center
                        transition: .4s ease-in-out all
                        em 
                            font-size: 20px
                            margin-right: 10px
                        &:hover
                            background: #0e2a4d
        @media (max-width: 768px)
            .box-text
                h2 
                    font-size: 25px
                p 
                    font-size: 16px