.mg-navigation
    .mobile-heading
        padding: 10px 30px
        font-size: 14px
        text-transform: uppercase
        font-weight: 500
        background: #ac1f23
        color: #fff
        position: relative
        display: none
        &:after
            content: '\e874'
            font-family: Linearicons-Free
            position: absolute
            top: 50%
            right: 30px
            transform: translateY(-50%)
    ul
        list-style: none
        justify-content: center
        padding-left: 0
        display: flex
        li
            margin: 0 5px
            &:hover, &.active
                a
                    background: #ac1f23
                    color: #fff
        a
            padding: 8px 30px
            text-transform: uppercase
            color: #999999
            display: block
            white-space: nowrap
            font-size: 14px
            background: #ebebeb
            transition: .4s ease-in-out all
            border-radius: 50px
    @media(max-width: 768px)
        .mobile-heading
            display: flex
        ul
            display: none
            flex-wrap: wrap
            display: none
            li
                margin: 0
                width: 100%
                a
                    border-radius: 0
    @media(min-width: 768px)
        ul
            display: flex!important