.doi-tac
    .partner-item
        figure
            position: relative
        .image
            img
                width: 100%
                height: 100%
                object-fit: cover
        figcaption
            position: absolute
            bottom: 0
            left: 0
            width: 100%
            padding: 10px
            z-index: 33
            background: rgba(#fff,.8)
            text-align: center
            .logo
                display: flex
                justify-content: center
                align-items: center
                img
                    height: 50px
    .swiper-navigation
        display: flex
        justify-content: center
        margin-top: 30px
    .swiper-btn
        width: 40px
        height: 40px
        display: flex
        transition: .4s ease-in-out all
        justify-content: center
        align-items: center
        font-size: 18px
        color: #999999
        border: 1px solid #e1e1e1
        margin: 0 5px
        &:focus
            outline: none
        &:hover
            background: #ac1f23
            color: #fff
    @media(min-width: 1200px)
        .partner-slider
            position: relative
        .swiper-navigation
            margin-top: 0
        .swiper-btn
            position: absolute
            top: 50%
            transform: translateY(-50%)
        .swiper-next
            left: calc(100% + 10px)
        .swiper-prev
            right: calc(100% + 10px)