.lien-he
    .information-wrapper
        height: 100%
        @media(min-width: 1025px)
            border-right: 1px solid #e1e1e1
        h3
            font-size: 20px
            font-weight: 600
            margin-bottom: 20px
            @media(min-width: 1025px)
                font-size: 24px
        .list-item
            .item
                margin-bottom: 20px
                display: flex
                align-items: flex-start
                span
                    display: block
                .ic
                    flex: 0 0 50px
                    display: flex
                    justify-content: center
                    align-items: center
                    margin-right: 20px
                p
                    font-weight: 600
                    text-transform: uppercase
                    span
                        font-weight: 400
                        margin-top: 15px
                        text-transform: initial
                        color: #666666
                &:last-child
                    margin-bottom: 0
    .note
        margin-top: 30px
        font-size: 14px
        color: #666666
    .map-wrapper
        margin-top: 30px
        iframe
            border: 2px solid #ac1f23
            min-height: 300px
            @media(min-width: 1025px)
                height: 480px