*
	padding: 0
	margin: 0
*
    &::-webkit-scrollbar
        width: 6px
    &::-webkit-scrollbar-track
        background: #eee
    &::-webkit-scrollbar-thumb
        background: #ac1f23
html
    font-family: 'Montserrat', sans-serif
    background-color: #ffff
    font-size: 14px
    // font-weight: 500
body
    font-family: 'Montserrat', sans-serif
    overflow-x: hidden
    font-size: 14px
    // font-weight: 500
    background-color: #fff
    main 
        padding-top: 88px
a
    color: inherit
    &:hover
        color: inherit
.mg-title
    text-align: center
    font-size: 24px
    text-transform: uppercase
    margin-bottom: 20px
    font-weight: 600
    @media(min-width: 1025px)
        font-size: 30px
    &.white
        color: #fff
.mg-sub-title
    text-transform: uppercase
    font-weight: 600
    font-size: 18px
    color: #ac1f23
    margin-bottom: 10px
.mg-section
    padding: 30px 0
    @media(min-width: 1025px)
        padding: 50px 0
.plus-icon
    display: inline-flex!important
    align-items: center
    &:after
        margin-left: 10px
        content: url('/Data/Sites/1/media/plus.png')
        display: flex
        justify-content: center
        align-items: center
        line-height: 1
        transition: .4s ease-in-out all
        display: inline-block
    &.silver
        &:after
            filter: grayscale(100%)
    &.white
        &:after
            filter: brightness(0) invert(1)
        &:hover
            &:after
                filter: initial
.mg-button
    display: inline-block
    padding: 8px 30px
    background: #ac1f23
    border-radius: 25px 
    font-size: 14px
    color: #ffffff
    transition: .4s ease-in-out all
    font-weight: 600
    text-transform: uppercase
    em 
        margin-left: 10px
    &:hover
        box-shadow: 0 0 0 2px inset #ac1f23
        background: none
        color: #ac1f23
.col-lg-20
    flex: 0 0 20%
.custom-select
    position: relative
    height: 40px
    margin-bottom: 10px
    border-radius: 5px
    overflow: hidden
    width: 100%
    span
        display: none
    select
        padding: 0 20px
        background: #ac1f23
        border: 0
        height: 100%
        width: 100%
        color: #fff
        font-size: 15px
        -webkit-appearance: none
        -moz-appearance: none
    &:after
        content: '\f0dd'
        width: 40px
        pointer-events: none
        height: 40px
        position: absolute
        top: 0
        right: 0
        display: flex
        justify-content: center
        // align-items: center
        padding-top: 5px
        background: #710c0e
        color: #fff
        font-family: Font Awesome\ 5 Free
        font-weight: 900
@-webkit-keyframes CircleAnimate
    0%
        width: 100%
        height: 100%
        opacity: 1
    50%
        width: 125%
        height: 125%
        opacity: .5
    100%
        width: 150%
        height: 150%
        opacity: 0
img
    max-width: 100%
    object-fit: cover
.pages.productdetailpager
    display: none!important